import { Component, OnInit, Inject } from '@angular/core';
import { Router } from '@angular/router';

import { TbAuthService } from '../auth.service';

import { LoginRequest } from '../models/login-request';
import { StorageVars } from '../models/storage-vars';
import { TbAuthEnvironment } from '../models/auth-environment';
import { Subscription } from '../models/subscription.model';

@Component({
    selector: 'tb-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss']
})
export class TbLoginComponent implements OnInit {
    private cachedCompanies: Array<any> = [];

    capsLockOn = false;
    loading = false;
    loginRequest: LoginRequest;
    redirectUrl: string;
    subscriptionSelection: boolean; // abilita la scelta della subscription
    loginSubscriptions: Array<{ description: string; subscriptionkey: string }> = [];

    logoUrl: string;

    logoB64 =
        // tslint:disable-next-line: max-line-length
        'data:image/png;base64,';

    cloudlogoB64 =
        // tslint:disable-next-line: max-line-length
        'data:image/png;base64,';

    constructor(public authService: TbAuthService, public router: Router, @Inject('env') env: TbAuthEnvironment) {
        this.loginRequest = new LoginRequest();
        this.loginRequest.appId = env.auth.appid;
        this.subscriptionSelection = env.auth.subs ? env.auth.subs : false;
        this.redirectUrl = env.auth.redirectUrl ? env.auth.redirectUrl : '/';

        this.logoUrl = env.auth.customLogo ? env.auth.customLogo : this.logoB64;
    }

    ngOnInit() {
        this.loadLoginData();
    }

    keyUpFunction(event: KeyboardEvent) {
        if (event.key === 'Enter') {
            if (
                this.loginRequest.accountName &&
                ((this.subscriptionSelection && this.loginSubscriptions.length > 0) || !this.subscriptionSelection)
            )
                this.login();
        }
        const capsOn = event.getModifierState && event.getModifierState('CapsLock');
        this.capsLockOn = capsOn;
    }

    disabledButton() {
        return !this.loginRequest.accountName || (this.subscriptionSelection && this.loginSubscriptions.length === 0) || this.loading;
    }

    accountNameBlur() {}

    async login() {
        this.authService.errorMessage = '';
        this.saveLoginData();
        this.loading = true;

        const result = await this.authService.login(this.loginRequest).catch(err => {
            this.loading = false;
            this.authService.errorMessage = err.error && err.error.Message;
            return;
        });

        this.loading = false;

        if (!result) return;

        // todo controlla come vengono mostrati errori sia login sia checkdb
        if (result.Result) {
            this.authService.errorMessage = '';
            this.router.navigate([this.redirectUrl]);
        } else {
            this.loading = false;
        }
    }

    loadLoginData() {
        this.loginRequest.accountName = this.authService.getAccountName() || '';
        this.loginRequest.subscriptionKey = this.authService.getSubscription() || '';

        if (!this.loginRequest.accountName) {
            this.loginRequest.subscriptionKey = '';
        } else {
            this.getCompaniesForUser(this.loginRequest.accountName);
        }
    }

    private saveLoginData() {
        localStorage.setItem(StorageVars.ACCOUNT_NAME, this.loginRequest.accountName);
        localStorage.setItem(StorageVars.SUBSCRIPTION, this.loginRequest.subscriptionKey);
    }

    async getCompaniesForUser(user: string) {
        if (!user || !this.subscriptionSelection) {
            this.loginRequest.subscriptionKey = '';
            return;
        }

        this.loginSubscriptions = [];
        this.loginRequest.subscriptionKey = '';
        this.authService.errorMessage = '';

        const temp = await this.requestAndSortSubscriptions(user);
        // Premio Eleganza Codice 2019 (@LucaBruni)
        if (JSON.stringify(temp) !== JSON.stringify(this.loginSubscriptions)) this.loginSubscriptions = temp;

        if (this.loginSubscriptions.length === 0) {
            this.loginRequest.subscriptionKey = '';
            localStorage.removeItem('_subscription'); // TODO fix naming
            localStorage.removeItem('_company'); // TODO fix naming
            this.authService.errorMessage = 'No Subscriptions associated to this user';
        }

        if (this.loginSubscriptions.length > 0) {
            if (!this.loginRequest.subscriptionKey) {
                this.loginRequest.subscriptionKey = this.loginSubscriptions[0].subscriptionkey;
            } else {
                if (this.loginSubscriptions.map(s => s.subscriptionkey).indexOf(this.loginRequest.subscriptionKey) === -1) {
                    this.loginRequest.subscriptionKey = this.loginSubscriptions[0].subscriptionkey;
                }
            }
        }
    }

    private async requestAndSortSubscriptions(user: string): Promise<Array<Subscription>> {
        if (this.cachedCompanies.hasOwnProperty(user) && this.cachedCompanies[user as any]) {
            return this.cachedCompanies[user as any];
        }

        const temp: Array<Subscription> = [];
        const result: Array<Subscription> = await this.authService.getCompaniesForUser(user).toPromise();
        result.sort(this.compareCompanies).forEach(c => {
            temp.push({ subscriptionkey: c.subscriptionkey, description: c.description });
        });

        if (temp.length > 0) this.cachedCompanies[user as any] = temp;

        return temp;
    }

    compareCompanies(c1: Subscription, c2: Subscription) {
        if (c1.description > c2.description) {
            return 1;
        } else if (c1.description < c2.description) {
            return -1;
        }
        return 0;
    }
}
